@import "../../../../assets/styles/app";

.buy {

  .cardTitle {
    font-weight: 500;
    font-size: 24px;
    line-height: 125%;
    text-align: center;
    color: #fff;
  }

  .currency {
    margin-top: 8px;
    @include Hind;
    font-size: 16px;
    line-height: 140%;
    text-align: center;
    color: rgba(255, 255, 255, 0.4);
  }

  .line {
    position: relative;
    margin-top: 32px;
    height: 7px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 999px;

    .lineInner {
      position: relative;
      height: 100%;
      width: 75%;
      background: linear-gradient(270deg, #dcff06 0%, rgba(226, 253, 38, 0) 100%);
      border-radius: 0 999px 999px 0;

      &::after {
        position: absolute;
        right: 0;
        top: 0;
        height: 100%;
        width: 7px;
        content: "";
        display: block;
        border-radius: 50%;
        filter: blur(9.399999618530273px);
        background: #faec5a;
      }
    }
  }

  .lineDescription {
    margin-top: 12px;
    display: flex;
    justify-content: space-between;

    p {
      font-weight: 500;
      font-size: 16px;
      line-height: 125%;
      color: #fff;
    }
  }

  .info {
    margin-top: 24px;
    display: flex;
    align-items: center;

    svg {
      width: 16px;
      height: 16px;
    }

    span {
      @include Hind;
      font-size: 14px;
      line-height: 125%;
      color: rgba(255, 255, 255, 0.45);
      margin-left: 4px;
    }
  }

  .form {
    margin-top: 8px;

    .payField {
      height: 70px;
      border: 1px solid rgba(255, 255, 255, 0.1);
      border-radius: 20px;
      padding-left: 20px;
      padding-right: 15px;
      display: flex;
      align-items: center;
      justify-content: space-between;

      .left {

        p:nth-child(1) {
          font-size: 14px;
          line-height: 125%;
          color: rgba(255, 255, 255, 0.5);
        }

        p:nth-child(2) {
          font-weight: 500;
          font-size: 16px;
          line-height: 125%;
          color: #fff;
        }
      }
    }

    .receiveField {
      margin-top: 8px;
      position: relative;

      .select {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 15px;
      }
    }

    .submitBtn {
      margin-top: 24px;
      height: 60px;
    }

  }

  .howToBuyLink {
    display: block;
    margin-top: 12px;
    text-decoration: none;
    @include Hind;
    font-size: 16px;
    line-height: 120%;
    letter-spacing: -0.02em;
    text-align: center;
    color: rgba(255, 255, 255, 0.5);
    transition: 0.3s;

    &:hover {
      @include desktop {
        color: rgba(255, 255, 255, 1);
      }
    }

  }
}