@import "../../../assets/styles/app";

.valueLabelComponent {
    padding: 24px 16px;
    width: 209px;
    height: 130px;
    //backdrop-filter: blur(32px);
    //background: #1B1D1C;

    svg {
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
    }

    .infos {
      position: relative;
      z-index: 2;
      display: flex;
      justify-content: space-between;

      & > div {

        &:nth-child(1) {
          border-right: 1px solid rgba(255, 255, 255, 0.1);
          padding-right: 12px;
        }

        p:nth-child(1) {
          font-size: 14px;
          line-height: 125%;
          color: rgba(255, 255, 255, 0.5);
        }

        p:nth-child(2) {
          margin-top: 2px;
          font-weight: 500;
          font-size: 16px;
          line-height: 125%;
          text-align: center;
          color: #fff;
        }
      }
    }

    .btn {
      position: relative;
      z-index: 2;
      margin-top: 12px;
      width: 100%;
      height: 38px;
    }

}