@import "../../assets/styles/app";

.joinTheFix {
  @include container;
  position: relative;
  padding-top: 128px;

  @include desktop {
    padding-top: 136px;
  }

  .linesWrapper {
    position: absolute;
    z-index: 1;
    width: 320px;
    height: 320px;

    &:nth-child(1) {
      top: 0;
      left: 50%;
      transform: translateX(-50%);

      @include desktop {
        left: calc(50% - 440px);
        transform: translateX(-50%);
      }
    }

    &:nth-child(2), &:nth-child(3) {
      @include onlyDesktop();
      width: 240px;
      height: 240px;
    }

    &:nth-child(2) {
      left: 0;
      bottom: 0;
    }

    &:nth-child(3) {
      right: 0;
      bottom: 0;
    }
  }

  .back {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
  }

  .inner {
    @include inner;
    position: relative;
    z-index: 2;

    .label {
      white-space: nowrap;
      padding: 8px 24px;
      background: rgba(255, 255, 255, 0.1);
      border-radius: 999px;

      @include desktop {
        margin-left: auto;
        margin-right: auto;
        width: 370px;
      }

      p {
        font-weight: 500;
        font-size: 16px;
        line-height: 125%;
        color: #fff;
        text-align: center;

        span {
          color: rgba(255, 255, 255, 0.5);
        }
      }

    }

    .title {
      margin-top: 24px;

      @include desktop {
        margin-top: 16px;
      }

      p {
        @include Hind;
        font-size: 52px;
        line-height: 85%;
        letter-spacing: -0.02em;
        text-align: center;
        color: #fff;

        @include desktop {
          font-size: 120px;
        }

        &:nth-child(2) {
          margin-top: 6px;

          @include desktop {
            margin-top: 0;
          }

          svg {
            width: 48px;
            height: 48px;

            @include desktop {
              width: 65px;
              height: 65px;
            }
          }

        }
      }
    }

    .content {
      margin-top: 32px;

      @include desktop {
        margin-top: 48px;
        display: flex;
        flex-direction: row-reverse;
        border: 1px solid rgba(255, 255, 255, 0.1);
        border-radius: 20px;
        width: 957px;
        margin-left: auto;
        margin-right: auto;

      }

      .contentTop {
        display: flex;
        flex-direction: column;

        @include desktop {
          width: 364px;
          flex-shrink: 0;
          padding-top: 8px;
          padding-right: 8px;
          padding-bottom: 24px;
        }

        .viewLeaderboard {
          align-self: center;
          margin-top: 24px;
          display: flex;
          align-items: center;
          text-decoration: none;

          svg {
            width: 24px;
            height: 24px;
          }

          p {
            @include Hind;
            font-weight: 500;
            font-size: 18px;
            line-height: 120%;
            letter-spacing: -0.02em;
            color: #fff;
            margin-left: 6px;
          }
        }

      }

      .contentBottom {
        margin-top: 48px;

        @include desktop {
          margin-top: 0;
          flex-grow: 1;
        }

        .descriptionTop {

          @include desktop {
            padding-top: 24px;
            padding-left: 24px;
            padding-bottom: 16px;
            border-bottom: 1px solid rgba(255, 255, 255, 0.1);

            @include desktop {
              border-bottom: none;
              position: relative;

              &::after {
                content: "";
                display: block;
                position: absolute;
                left: 0;
                bottom: 0;
                height: 1px;
                width: 100%;
                opacity: 0.1;
                background: linear-gradient(90deg, #FFF, rgba(255, 255, 255, 0.5));
              }
            }

          }

          p {
            font-size: 18px;
            line-height: 140%;
            text-align: center;
            color: rgba(255, 255, 255, 0.4);

            @include desktop {
              width: 497px;
              text-align: left;
            }
          }

          span {
            color: #fff;
          }


        }

        .groups {
          margin-top: 24px;

          @include desktop {
            margin-top: 0;
          }

          .group {
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding-bottom: 20px;
            border-bottom: 1px solid rgba(255, 255, 255, 0.1);

            @include desktop {
              justify-content: flex-start;
              padding-bottom: 26px;
              padding-left: 24px;
              border-bottom: none;
              position: relative;

              &::after {
                content: "";
                display: block;
                position: absolute;
                left: 0;
                bottom: 0;
                height: 1px;
                width: 100%;
                opacity: 0.1;
                background: linear-gradient(90deg, #FFF, rgba(255, 255, 255, 0.5));
              }


            }

            &:not(:first-child) {
              padding-top: 16px;
            }

            &:first-child {
              @include desktop {
                padding-top: 26px;
              }
            }

            .left {
              display: flex;
              align-items: center;

              p:nth-child(1) {
                font-weight: 500;
                font-size: 20px;
                line-height: 125%;
                color: #fff;
              }

              p:nth-child(2) {
                @include centerRow;
                width: 57px;
                height: 29px;
                border-radius: 999px;
                background: #f7fa5a;
                margin-left: 12px;

                @include desktop {
                  width: 90px;
                  height: 29px;
                  margin-left: 16px;
                }

                span {
                  font-weight: 500;
                  font-size: 16px;
                  line-height: 140%;
                  letter-spacing: -0.04em;
                  color: #000;

                  &:nth-child(1) {
                    @include onlyDesktop(inline);
                    margin-right: 4px;
                  }
                }

              }
            }

            .right {

              @include desktop {
                display: flex;
                margin-left: 16px;
              }

              p {
                font-size: 18px;
                line-height: 140%;

                &:nth-child(1) {
                  font-weight: 500;
                  color: #fff;
                }

                &:nth-child(2) {
                  font-weight: 400;
                  color: rgba(255, 255, 255, 0.4);
                  margin-left: 6px;
                }
              }
            }

          }


        }

        .descriptionBottom {
          @include Hind;
          font-size: 18px;
          line-height: 140%;
          text-align: center;
          color: rgba(255, 255, 255, 0.4);
          margin-top: 24px;

          @include desktop {
            width: 497px;
            margin-top: 20px;
            padding-left: 24px;
            padding-bottom: 24px;
            text-align: left;
          }

        }


      }

    }

  }
}